<template>
  <div class="login">
    <div class="login-top">
      <div class="top-txt">
        <h1>预警一体化平台</h1>
        <p>Integrated early warning platform</p>
      </div>
    </div>

    <div class="content">
      <p class="title">登录</p>
      <el-divider></el-divider>
      <el-input v-model="user" placeholder="请输入账号"></el-input>
      <el-input placeholder="请输入密码" v-model="pass" show-password @keyup.enter.native='login'></el-input>
      <el-button type="primary" @click='login'>登录</el-button>
    </div>


  </div>
</template>

<script>
  import {login} from '../api/login/login.js'
  export default{
    data(){
      return{
        user: '',
        pass: ""
      }
    },
    mounted(){
      /* login({
        account: 'test1',
        password: 123456
      }).then(res => {
        console.log(res)
      }) */
    },
    methods: {
      login(){
        if(this.user == 123&&this.pass == 123){
          this.$message({
            message: '恭喜你，登录成功了吖',
            type: 'success',
            duration: 1500
          });
          localStorage.flag = '1';
          let path = '/';
          if(this.$route.query.path){
            path = this.$route.query.path;
          }
          setTimeout(() => {
            this.$router.push({
              path
            })
          },1500);
        }else{
          this.$message.error({
            message: '账号或密码错了呦。',
            duration: 1500
          });
          this.pass = '';
        }
      }
    }
  }
</script>

<style scoped lang='less'>
  .login{
    position: relative;
    width: 100vw;
    height: 100vh;
    background: url(../assets/imgs/login/bg.png);
    background-size: 100% 100%;
    .content{
      width: 470px;
      height: 470px;
      margin: 0 auto;
      background: #fff;
      border-radius: 20px;
      position: relative;
      top: 250px;
      box-sizing: border-box;
      padding: 40px 30px;
      .title{
        text-align: center;
        height: 50px;
        line-height: 50px;
        color: #080808;
        font-weight: bold;
      }
      .el-divider{
        margin-bottom: 40px;
      }
      .el-input{
        margin-bottom: 20px;
      }
      .el-button{
        width: 100%;
        background: rgb(4,8,68);
        height: 50px;
      }
    }
    .login-top{
      width: 100%;
      height: 105px;
      background: #fff;
      position: absolute;
      top: 0;
      .top-txt{
        height: 105px;
        margin-top: 15px;
        text-indent: 200px;
        h1{
          font-size: 28px;
          font-family: Microsoft YaHei;
          font-weight: bold;
          color: #323333;
        }
        p{
          font-size: 16px;
          font-family: Microsoft YaHei;
          font-weight: 400;
          color: #323333;
        }
      }
    }
  }
</style>
<style>
  .el-input__inner{
    background: rgb(241,241,241);
    height: 45px;
  }
</style>
