




































































.login{
  position: relative;
  width: 100vw;
  height: 100vh;
  background: url(../assets/imgs/login/bg.png);
  background-size: 100% 100%;
  .content{
    width: 470px;
    height: 470px;
    margin: 0 auto;
    background: #fff;
    border-radius: 20px;
    position: relative;
    top: 250px;
    box-sizing: border-box;
    padding: 40px 30px;
    .title{
      text-align: center;
      height: 50px;
      line-height: 50px;
      color: #080808;
      font-weight: bold;
    }
    .el-divider{
      margin-bottom: 40px;
    }
    .el-input{
      margin-bottom: 20px;
    }
    .el-button{
      width: 100%;
      background: rgb(4,8,68);
      height: 50px;
    }
  }
  .login-top{
    width: 100%;
    height: 105px;
    background: #fff;
    position: absolute;
    top: 0;
    .top-txt{
      height: 105px;
      margin-top: 15px;
      text-indent: 200px;
      h1{
        font-size: 28px;
        font-family: Microsoft YaHei;
        font-weight: bold;
        color: #323333;
      }
      p{
        font-size: 16px;
        font-family: Microsoft YaHei;
        font-weight: 400;
        color: #323333;
      }
    }
  }
}
