.login {
  position: relative;
  width: 100vw;
  height: 100vh;
  background: url(../assets/imgs/login/bg.png);
  background-size: 100% 100%;
}
.login .content {
  width: 470px;
  height: 470px;
  margin: 0 auto;
  background: #fff;
  border-radius: 20px;
  position: relative;
  top: 250px;
  box-sizing: border-box;
  padding: 40px 30px;
}
.login .content .title {
  text-align: center;
  height: 50px;
  line-height: 50px;
  color: #080808;
  font-weight: bold;
}
.login .content .el-divider {
  margin-bottom: 40px;
}
.login .content .el-input {
  margin-bottom: 20px;
}
.login .content .el-button {
  width: 100%;
  background: #040844;
  height: 50px;
}
.login .login-top {
  width: 100%;
  height: 105px;
  background: #fff;
  position: absolute;
  top: 0;
}
.login .login-top .top-txt {
  height: 105px;
  margin-top: 15px;
  text-indent: 200px;
}
.login .login-top .top-txt h1 {
  font-size: 28px;
  font-family: Microsoft YaHei;
  font-weight: bold;
  color: #323333;
}
.login .login-top .top-txt p {
  font-size: 16px;
  font-family: Microsoft YaHei;
  font-weight: 400;
  color: #323333;
}
